<template>
  <div class="container-fluid">
    <div class="card card-custom mb-0">
      <div class="card-header border-0 pt-5 pb-0">
        <div class="flex-fill">
          <h3 class="card-title align-items-start flex-column">
            <span class="card-label font-weight-bolder text-dark mb-3">Template Gaji Tenaga Kerja</span>
          </h3>
          <div class="d-flex mt-3">
            <div class="d-flex align-items-center mr-3">
              <span class="d-block mr-3 font-size-sm">Periode Penggajian :</span>
              <s-input v-model="selectedPeriod" :item="{ nm:true, label: '', defaultCaption:'Pilih Periode Penggajian', type:'lookup-radio', validation:[], placeholder: '', api:'payroll-periodes', pointer: { label: 'periode_month|period', code: 'periode_month', display: ['periode_month|period', 'count_day_of_work|count_day_of_work'] } }" />
            </div>
            <div class="d-flex align-items-center mr-3">
              <span class="d-block mr-3 font-size-sm">Komponen :</span>
              <s-input v-model="selectedCategory" :item="{ ic:'min-w-300px', nm:true, label: '', defaultCaption:'Pilih Komponen', type:'select', validation:[], placeholder: 'Pilih Kategori Komponen', api:'master-salary-components', pointer: { label: 'name', code: 'id', display: ['name', 'rel_salary_component_category_id'] } }" />
            </div>
            <div class="d-flex flex-fill align-items-center mr-3">
              <!-- <span class="d-block mr-3 font-size-sm">Komponen :</span> -->
              <div class="form-group mb-0  flex-fill">
                <input v-model.lazy="inputSearch" type="text" class="form-control" placeholder="Cari Nama / Jabatan / Status Tenaga kerja">
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card-body pt-3">
        <div v-if="mainData.length" id="template-gaji-sheet" class="scrolling-table">
          <table ref="template-gaji-sheet" class="table v-center header-center header-uppercase template-gaji-sheet table-custom-header mb-0">
            <thead class="bg-light-primary">
              <tr>
                <th rowspan="2" class="nowrap-table bg-light-primary">No</th>
                <th rowspan="2" class="min-w-250px sticky-left bg-light-primary">Nama Tenaga Kerja</th>
                <th rowspan="2" class="">Kategori Jabatan</th>
                <th rowspan="2" class="">Jabatan</th>
                <th rowspan="2" class="">Status Ketenagakerjaan</th>
                <template v-for="(h,i) in mainData[0].data_salary">
                  <th v-if="h.data_salary_components.length>1" :key="i+'-header-template'" :colspan="h.data_salary_components.length">{{ h.name }}</th>
                  <th v-else :key="i+'-header-template'" :rowspan="2" :class="h.data_salary_components[0].id === selectedCategory ? 'bg-light-info': ''" :ref="h.data_salary_components[0].id+'-komponen-gaji'" :id="h.data_salary_components[0].id+'-komponen-gaji'">{{ h.name }}</th>
                </template>
              </tr>
              <tr>
                <template v-for="(h,i) in mainData[0].data_salary.filter(x=> x.data_salary_components.length>1)">
                  <th v-for="(s,j) in h.data_salary_components" :key="j+'-'+i+'-header-template'" style="top:28.59px" :class="s.id === selectedCategory ? 'bg-light-info': ''" :ref="s.id+'-komponen-gaji'" :id="s.id+'-komponen-gaji'"> {{ s.name }} </th>
                </template>
              </tr>
            </thead>
            <tbody>
              <template v-for="(d,i) in mainData">
                <tr :key="i+'-data-template'">
                  <td class="nowrap-table text-right bg-light-primary">{{ i+1 }}.</td>
                  <td @click="setFullWidth('rel_employee_id')" class="sticky-left bg-light-primary font-weight-bold" :class="fullwidthColumns.includes('rel_employee_id') ? 'nowrap-table': 'max-w-170px'">
                    <div class="text-line-1"> {{ d.rel_employee_id }} </div>
                  </td>
                  <td @click="setFullWidth('rel_job_position_category_id')" class="pointer" :class="fullwidthColumns.includes('rel_job_position_category_id') ? 'nowrap-table': ''">
                    <div class="text-line-1">{{ d.rel_job_position_category_id }} </div>
                  </td>
                  <td @click="setFullWidth('rel_job_position_id')" class="pointer" :class="fullwidthColumns.includes('rel_job_position_id') ? 'nowrap-table': ''">
                    <div class="text-line-1"> {{ d.rel_job_position_id }} </div>
                  </td>
                  <td @click="setFullWidth('rel_status_employment_id')" class="pointer" :class="fullwidthColumns.includes('rel_status_employment_id') ? 'nowrap-table': ''">
                    <div class="text-line-1"> {{ d.rel_status_employment_id }} </div>
                  </td>
                  <template v-for="(h,j) in d.data_salary">
                    <td v-for="(s,k) in h.data_salary_components" :key="k+'-'+j+'-'+i+'-body-template'" class="input-td">
                      <template v-if="s.active === 1">
                        <div class="d-flex justify-content-between align-items-center">
                          <vue-numeric v-model.lazy="s.default_value" @change="changedValue(d.employee_id, s)" separator="." :empty-value="0" :currency="'Rp.'" :precision="2" class="form-control form-control-sm" />
                          <button @click="updateActive(d.employee_id, s)" class="btn btn-sm btn-block btn-light-danger btn-icon">
                            <i class="ri-shut-down-line"></i>
                          </button>
                        </div>
                      </template>
                      <template v-else>
                        <button @click="updateActive(d.employee_id, s)" class="btn btn-sm btn-block btn-light-primary">Aktifkan Komponen</button>
                      </template>
                    </td>
                  </template>
                </tr>
              </template>
            </tbody>
            <!-- <tfoot>
              <tr>
                <th class="nowrap-table bg-light-primary"></th>
                <th class="text-right sticky-left bg-light-primary">
                  <div class="d-flex justify-content-between">
                    <span>{{mainData.length}} Tenaga Kerja</span>
                    <span class="text-right">Total</span>
                  </div>
                </th>
                <th class="" colspan="3"></th>
                <template v-for="(h,i) in mainData[0].data_salary">
                  <th v-for="(s,j) in h.data_salary_components" :key="j+'-'+i+'-footer-total'" class="">
                    <div class="d-flex justify-content-between">
                      <span class="text-capitalize">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ mainData.map(a => a.data_salary[i].data_salary_components[j].value).filter(b => b).reduce((acc, curr) => acc + curr, 0) | parse('number2fraction') }}</span>
                    </div>
                  </th>
                </template>
              </tr>
            </tfoot> -->
          </table>
        </div>
        <div v-if="mainData.length>0" class="d-flex justify-content-between align-items-center flex-wrap">
          <b-pagination class="mb-0" size="md" prev-text="Sebelumnya" next-text="Selanjutnya" hide-goto-end-buttons v-model="paramAPI.page" :total-rows="total" :per-page="paramAPI.limit" />
          <div class="d-flex align-items-center py-3">
            <div v-if="isLoading" class="d-flex align-items-center">
              <div class="mr-2 text-muted">{{ $t('global.loading') }} ...</div>
              <div class="spinner spinner-primary mr-10"></div>
            </div>
            <v-select class="limit-table mr-4" placeholder="Limit Data" v-model="paramAPI.limit" :value="paramAPI.limit" append-to-body :searchable="false" :clearable="false" :options="[10,20,50,100]"></v-select>
            <span class="text-muted">{{ detailPagination }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'template-setup-gaji',
  data () {
    return {
      config: {
        getter: 'custom/mapping-template-salary-employees-table',
        setter: 'custom/mapping-template-salary-employees-table',
        permission: {
          create: 'create-control-sheet-payroll',
          read: 'view-control-sheet-payroll',
          update: 'update-control-sheet-payroll',
          delete: 'delete-control-sheet-payroll'
        }
      },
      selectedPeriod: null,
      selectedCategory: null,
      fullwidthColumns: [],
      total: 0,
      isLoading: false,
      inputSearch: null,
      mainData: [],
      paramAPI: {
        page: 1,
        limit: 20,
        sort_by: '',
        sort: 'DESC',
        search: '',
        filters: {}
      }
    }
  },
  computed: {
    detailPagination () {
      let _inPage = (this.paramAPI.limit > this.mainData.length) ? this.mainData.length : this.paramAPI.limit
      return this.$t('global.display') + ' ' + _inPage + ' ' + this.$t('global.of') + ' ' + this.total + ' ' + this.$t('global.record')
    }
  },
  watch: {
    selectedCategory: {
      deep: false,
      immediate: false,
      handler (to) {
        if (to) {
          var VueScrollTo = require('vue-scrollto')
          const componen = this.$refs[to + '-komponen-gaji']
          VueScrollTo.scrollTo(componen[0], 500, {
            container: '#template-gaji-sheet',
            force: true,
            lazy: false,
            offset: -250,
            x: true,
            y: false
          })
        }
      }
    },
    selectedPeriod: {
      deep: false,
      immediate: true,
      async handler (to) {
        if (to) {
          this.$store.commit('set', ['lastSelectedPeriodPayroll', to, true])
          this.getListData(this.paramAPI)
        }
      }
    },
    inputSearch: {
      deep: false,
      immediate: false,
      handler (to) {
        let param = Object.assign({}, this.paramAPI)
        param.page = 1
        param.search = to
        this.paramAPI = Object.assign({}, param)
      }
    },
    filter: {
      deep: true,
      immediate: true,
      handler (to) {
        let param = Object.assign({}, this.paramAPI)
        param.page = 1
        param.filters = to
        this.paramAPI = Object.assign({}, param)
      }
    },
    paramAPI: {
      deep: true,
      immediate: false,
      handler (to) {
        this.getListData(to)
      }
    }
  },
  beforeMount () {
    this.setThisMonth()
  },
  methods: {
    async setThisMonth () {
      const alreadySelect = this.$store.state.lastSelectedPeriodPayroll
      if (!alreadySelect) {
        let data = await this.sendAPI('list', 'payroll-periodes', { periode_month: moment(new Date()).set({ 'date': 1 }).format('YYYY-MM-DD') })
        if (data.length) {
          this.selectedPeriod = data[0].periode_month
          this.$store.commit('set', ['lastSelectedPeriodPayroll', data[0].periode_month, false])
        }
      } else this.selectedPeriod = alreadySelect
    },
    setFullWidth (field) {
      if (this.fullwidthColumns.includes(field)) {
        this.fullwidthColumns = this.fullwidthColumns.filter(x => x !== field)
      } else this.fullwidthColumns.push(field)
    },
    async changedValue (id, val) {
      this.isLoading = true
      const data = {
        'periode_month': this.selectedPeriod,
        'employee_id': id,
        'salary_component_id': val.id,
        'default_value': val.default_value
      }
      await this.sendAPI('update', this.config.setter, data)
      //   this.$_alert.success('hehe')
      this.isLoading = false
    },
    async updateActive (id, val) {
      this.isLoading = true
      const newVal = val.active === 1 ? 0 : 1
      const data = {
        'active': newVal,
        'periode_month': this.selectedPeriod,
        'employee_id': id,
        'salary_component_id': val.id,
        'default_value': 0
      }
      if (newVal === 0) {
        this.$_alert.confirm('Non-Aktifkan Komponen', 'Nilai komponen akan dihapus ketika Komponen non-aktif, Lanjutkan?').then(async (result) => {
          if (result.isConfirmed) {
            await this.sendAPI('update', this.config.setter, data)
            val.active = newVal
            val.default_value = 0
          }
        })
      } else {
        await this.sendAPI('update', this.config.setter, data)
        val.active = newVal
        val.default_value = 0
      }
      this.isLoading = false
    },
    async getListData (globalFilter) {
      this.isLoading = true
      const filter = { ...{ periode_month: this.selectedPeriod }, ...globalFilter }
      const data = await this.sendAPI('list', this.config.getter, filter)
      this.total = data.total
      this.mainData = data.data
      this.isLoading = false
    },
    sendAPI (method, api, param) {
      return this.$_api[method](api, param).then(res => {
        return res
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>

<style>
  .w-fit-content {
    width: 100%;
  }
  .table.template-gaji-sheet th,
  .table.template-gaji-sheet td {
    width: 170px;
    min-width: 170px;
    padding: 0.35rem 0.75rem;
    border: 0 !important;
    box-shadow: inset 0 0 0 0.15px rgb(0 0 0 / 30%);
  }
  .table.template-gaji-sheet td {
    padding: 1px 0.75rem;
  }
  .table.template-gaji-sheet td:focus-within,
  .template-gaji-sheet .form-control:focus {
    background-color: #f4f1fe;
  }
  .template-gaji-sheet .form-control {
    padding-left: 0;
    padding-right: 0;
    border: none;
  }
  .sticky-left {
    position: sticky !important;
    left: -1px !important;
    background-color: #ffffff;
    z-index: 2;
    max-width: 250px;
    min-width: 170px;
  }
  .scrolling-table {
    display: block;
    width: 100%;
    max-height: 70vh;
    overflow-y: auto;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
  .scrolling-table thead th {
    position: sticky !important;
    top: 0;
    background-color: #ffffff;
    z-index: 3;
  }
  .scrolling-table tfoot th {
    position: sticky !important;
    bottom: 0;
    background-color: #ffffff;
    z-index: 3;
  }
  .scrolling-table tfoot th.sticky-left,
  .scrolling-table thead th.sticky-left {
    z-index: 4;
  }
  .input-td button {
    opacity: 0;
  }
  .input-td:hover button {
    opacity: 1;
  }
</style>



